export default {
  name: 'News',
  props: {
    newsList: {
      type: Array,
      default () {
        return []
      }
    }
  },
  methods: {
    navigateToDetail (item) {
      this.$router.push({
        name: 'newsCase-id',
        params: {
          id: item.news_id
        },
        query: {
          type: 'news'
        }
      })
    }
  }
}
